.title-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin: 0 0 50px 0;
}

.title-header {
    font-size: 32px;
    color: var(--dark-background);
    font-family: "Poppins Bold";
}

.section-sub-header {
    letter-spacing: 3px;
    margin: 8px 0 10px 0;
    font-size: 12px;
    color: var(--dark-background);
}

.title-separator {
    display: flex;
    align-items: center;
    position: relative;
    width: 180px;
    margin: 10px 0 0 0;
}

.separator-line {
    width: 100%;
    height: 2px;
    background-color: var(--dark-background);
}

.separator-blob {
    height: 10px;
    width: 100%;
    position: absolute;
    display: flex;
    justify-content: center;
    top: -3px
}

.blob {
    width: 35px;
    border-radius: 10px;
    background-color: var(--dark-orange);
}